import React from 'react';
import { compose } from 'recompose';
import c from 'classnames';
import truncate from 'lodash/truncate';
import closeSvg from '../../assets/close.svg';
import { Tag } from '../';

export default compose(
)(function Link({ onRemoveRequested, focus, blur, isFocused, id, url, title, description, tags, changeTagColor }) {
  return (
    <div
      tabIndex="1"
      className={c('outline-none row')}
      onBlur={blur}
    >
      <div className="col-10">
        <p className="lead mb-0">
          <a href={url} target="_blank">{title ? title : truncate(url)}</a>
        </p>
        <p>
          {description}
        </p>
        <div className="mb-1 lead">
          {tags.map(({ name, id, color }) =>
            // <span className="mr-1 badge badge-primary" key={id}>{name}</span>
            <Tag
              name={name}
              id={id}
              color={color}
              key={id}
              onChangeColorRequested={changeTagColor}
            />
          )}
        </div>
      </div>
      <div className="col-2 d-flex justify-content-end align-items-center">
        <img
          className="clickable"
          src={closeSvg}
          alt="close"
          width={40}
          height={40}
          onClick={() => onRemoveRequested(id)}
        />
      </div>
    </div>
  );
})



