import React, { Fragment } from 'react';
import { Navbar, NavbarToggler, NavbarBrand, Collapse, Nav, NavItem, NavLink, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem} from 'reactstrap';
import { withRouter } from 'react-router-dom';
import menuSvg from '../../assets/menu.svg';
import exitSvg from '../../assets/exit.svg';

function MenuButton({ children, ...rest }) {
  return (
    <div {...rest}>
      <img
        width={50}
        height={50}
        className="clickable"
        src={menuSvg}
        alt={'menu'}
      />
    </div>
  );
}

class Header extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }
  render() {
    const { user, history } = this.props;
    return (
      <Navbar className="bg-light container" fixed="top" color="faded" light expand="md">
        <NavbarBrand href="/">TaggyTag</NavbarBrand>
        <NavbarToggler tag={MenuButton} onClick={this.toggle}/>
        <Collapse isOpen={this.state.isOpen} navbar>
          <Nav className="ml-auto" navbar>
            <NavItem>
              <NavLink href="/chrome-extension/">Google Chrome Extension</NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="/instruction/">How to TaggyTag</NavLink>
            </NavItem>
            {user &&
              <UncontrolledDropdown nav>
                <DropdownToggle nav caret>
                  {user.email}
                </DropdownToggle>
                <DropdownMenu >
                  <DropdownItem onClick={() => {
                    this.toggle();
                    history.push('/logout');
                  }}>
                    <img className="mr-2" width="40" src={exitSvg} alt={'exit'}/>
                    Logout
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
            }
            {!user &&
              <Fragment>
                <NavItem>
                  <NavLink href="/login">Sign In</NavLink>
                </NavItem>
                <NavItem>
                  <NavLink href="/signup">Sign Up</NavLink>
                </NavItem>
              </Fragment>
            }
          </Nav>
        </Collapse>
      </Navbar>
    );
  }
}
export default withRouter(Header);



