import axios from 'axios';

export default function apiClient(data) {
  return axios
    .post(process.env.REACT_APP_GRAPHQL_ENDPOINT, data)
    .then(({ data }) => data)
    .then(({ data, errors }) => {
      if (data) return Promise.resolve(data);
      return Promise.reject(errors);
    });
}



