import React from 'react';
import { withHandlers, compose } from 'recompose';
import { connect } from 'react-redux';
import { Card, CardBody,
  Input, Button, FormGroup, InputGroup, InputGroupAddon } from 'reactstrap';
import { signUp, login } from '../../redux/modules/auth';
import envelopeSvg from '../../assets/envelope.svg';
import lockSvg from '../../assets/lock.svg';
import logoSvg from '../../assets/logo.svg';

export default compose(
  connect(
    null,
    {
      signUp,
      login
    }
  ),
  withHandlers({
    submit: ({ signUp }) => (e) => {
      e.preventDefault();
      const email = e.target.email.value;
      const password = e.target.password.value;
      const password2 = e.target.password2.value;
      if (password === password2) {
        signUp(email, password)
          .then(() => login(email, password));
      } else {
        alert('The 2 passwords are not identical!');
      }
    }
  })
)(function SignUp({ submit }) {
  return (
    <form className="container" onSubmit={submit}>
      <div className="row">
        <div className="col-12 col-md-6 offset-md-3">
          <Card>
            <CardBody>
              <div className="d-flex justify-content-center align-items-center mb-5">
                <img src={logoSvg} alt="logo" width="45"/>
                <h1 className="ml-2 d-inline">TaggyTag</h1>
              </div>
              <FormGroup>
                <InputGroup>
                  <InputGroupAddon>
                    <img src={envelopeSvg} alt={'envelope'} width="20"/>
                  </InputGroupAddon>
                  <Input name="email" type="text" placeholder="Email address"/>
                </InputGroup>
              </FormGroup>
              <FormGroup>
                <InputGroup>
                  <InputGroupAddon>
                    <img src={lockSvg} alt={'lock'} width="20"/>
                  </InputGroupAddon>
                  <Input name="password" type="password" placeholder="Password"/>
                </InputGroup>
              </FormGroup>
              <FormGroup>
                <InputGroup>
                  <InputGroupAddon>
                    <img src={lockSvg} alt={'lock'} width="20"/>
                  </InputGroupAddon>
                  <Input name="password2" type="password" placeholder="Retype password"/>
                </InputGroup>
              </FormGroup>
              <div className="text-center mt-4">
                <Button outline type="submit" color={'primary'}>Create an account</Button>
              </div>

              <p className="mt-5 text-center">Already have an account? <a href="/login">Login here</a></p>

              <p className="text-secondary text-center">@TaggyTag</p>
            </CardBody>
          </Card>
        </div>
      </div>
    </form>
  );
})



