import React from 'react';
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';

function Item({selected, color, ...rest}) {
  return <DropdownItem className={`bg-${color} text-white text-center pointer`} {...rest}>{selected ? 'Selected' : <span>&nbsp;</span>}</DropdownItem>
}

export default class Tag extends React.Component {
  state = {
    dropdownOpen: false
  };
  toggle = () => {
    this.setState({
      dropdownOpen: !this.state.dropdownOpen
    });
  };
  render() {
    const { id, name, color: tagColor, onChangeColorRequested = () => {} } = this.props;
    return (
      <Dropdown isOpen={this.state.dropdownOpen} toggle={this.toggle} className="d-inline">
        <DropdownToggle tag={'span'} className={`mr-1 badge badge-${tagColor.toLowerCase()} pointer`}>
          {name}
        </DropdownToggle>
        <DropdownMenu>
          {['PRIMARY', 'SUCCESS', 'DANGER', 'DARK', 'INFO', 'WARNING'].map((color, i) =>
            <Item
              key={color}
              onClick={() => onChangeColorRequested({ id, color })}
              color={color.toLowerCase()}
              selected={color === tagColor}/>
          )}
        </DropdownMenu>
      </Dropdown>
    );
  }
}



